import React, { Component } from 'react';
import '../App.css';
import Note from './Note';
import DropDown from './DropDown';
import styled from 'styled-components';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';

import { Link } from 'react-router-dom';

const StyledLink = styled(Link)`
  width: 300px;
  margin-bottom: 15px;
  margin-right: 30px;
  background: #fff;
  height: 300px;
  border: 1px solid #A9A9A9;
  border-radius: 2px;
  text-decoration: none;
  overflow: hidden;
  transition: all .2s ease-in-out;

  &:hover {
    transform: scale(1.05);
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  }
`;

	


class Notes extends Component {


  onDragEnd = result => {
    //
  }


  render() {
    console.log('notes list inside Notes.js', this.props.notes); 
    return (
    
        <div className="notes-container">
          <div className="heading-flex-wrapper">
            <h2 className="notes-heading">Your notes:</h2>
            <input className="main-input" type="text" placeholder="Search" value={this.props.filter} onChange={this.props.handleInputChange}/>
            <DropDown sortedByTitle={this.props.sortedByTitle} sortedByMostRecent={this.props.sortedByMostRecent} />
          </div>
          <DragDropContext onDragEnd={this.onDragEnd}>
            <Droppable droppableId="dnd-container">
            {(provided) => (
                <div className="note-cards"
                ref={provided.innerRef}
                {...provided.droppableProps}
                >

                {this.props.noteData.map((note, index) => {
                  return (
                    <Draggable draggableId={note._id} index={index} key={note._id || note.title}>
                    {(provided) => (
                    <div className="note"
                      {...provided.draggableProps}
                      {...provided.dragHandleProps}
                      ref={provided.innerRef}
                    >
                      <Link className="note-link"
                      to={`/get/${note._id}`}
                      
                      >
                        <Note
                          note={note}
                        />
                      </Link>
                    </div>
                    )}
                    </Draggable>
                  );
                })}
                {provided.placeholder}
              </div>
            )}
            </Droppable>
          </DragDropContext> 
        </div>
    );
  }
}


export default Notes;